import * as React from 'react';
import { connect } from 'react-redux';
import { ITodo } from '../../constants/Interface';
import TodoCard from '../smarts/Todocard';
import Grid from '@material-ui/core/Grid';


class Todos extends React.Component<IMapStateToProps, React.ComponentState> {
    public render() {
        return (
            <div>
                <Grid container={true} spacing={16} wrap='wrap' style={{margin:'auto'}}>
                {
                    this.props.Todos.map((x: ITodo, i: number) => {
                        return <TodoCard key={i} {...x} />
                    })
                }
                </Grid>
            </div>
        );
    }
}

interface IMapStateToProps {
    Todos: ITodo[]
}

function mapStateToProps(state: any) {
    return {
        Todos: state.Todos
    }
}


export default connect<IMapStateToProps, {}>(mapStateToProps, {})(Todos);



