import * as React from 'react';
import Typography from '@material-ui/core/Typography';
import Grid from '@material-ui/core/Grid';
import Card from '@material-ui/core/Card';
import CardContent from '@material-ui/core/CardContent';
import CardActions from '@material-ui/core/CardActions';
import Button from '@material-ui/core/Button';
import TextField from '@material-ui/core/TextField';
import { ITodo, ITodoAction } from '../../constants/Interface';
import action from '../../actions';
import { bindActionCreators, Dispatch } from 'redux';
import { connect } from 'react-redux';

interface IProps extends ITodo{
  delete:(todo:ITodo)=>ITodoAction;
  edit:(todo:ITodo)=>ITodoAction;
}

class TodoCard extends React.Component<IProps,React.ComponentState> {
  public state ={
    isEdite:false
  }

  public componentWillMount(){
    this.toggleEdite = this.toggleEdite.bind(this)
    this.onCancel = this.onCancel.bind(this)
    this.onDelete = this.onDelete.bind(this)
  }
  
  public render() {
    return (
      <Grid item={true} xs={12} sm={6} md={3} lg={2}>
        <Card>
          <CardContent>
            {
              this.state.isEdite?
              <form noValidate={true}>
                <TextField
                  id="datetime-local"
                  label="Next appointment"
                  type="datetime-local"
                  defaultValue={this.props.time}
                  InputLabelProps={{
                    shrink: true,
                  }}
                />
              </form>
              :
              <Typography color='textSecondary'>
                { (new Date(this.props.time)).toLocaleString() }
              </Typography>
            }
            <Typography color='textPrimary'>
              {this.props.title}
            </Typography>
            <Typography color='textSecondary'>
              {this.props.context}
            </Typography>
          </CardContent>
          {
            this.state.isEdite?
            <CardActions>
              <Button size="small" color="primary" onClick={this.toggleEdite}>
                Confirm
              </Button>
              <Button size="small" color="primary" onClick={this.onCancel}>
                Cancel
              </Button>
            </CardActions>
            :
            <CardActions>
              <Button size="small" color="primary" onClick={this.toggleEdite}>
                Edit
              </Button>
              <Button size="small" color="primary" onClick={this.onDelete}>
                Delete
              </Button>
            </CardActions>
          }
        </Card>
      </Grid>
    );
  }

  private toggleEdite(){
    this.setState({isEdite:!this.state.isEdite})
  }

  private onCancel(){
    this.setState({isEdite:false})
  }

  private onDelete(){
    this.props.delete({
      id:this.props.id,
      time:this.props.time,
      title:this.props.title,
      context:this.props.context,
      completed:this.props.completed
    })
  }

}

function mapDispatchToProps(dispatch: Dispatch) {
  return bindActionCreators({
      edit: action.todos.EditeTodo,
      delete: action.todos.DeleteTodos
  }, dispatch)
}

export default connect(null,mapDispatchToProps)(TodoCard);



